import * as React from 'react';
import { ImageListItemBar, ImageList, ImageListItem, Box, useTheme, useMediaQuery } from '@mui/material';

function srcset(image, size, rows = 1, cols = 1) {
  return {
    src: `${image}`,
    srcSet: `${image}?w=${size * cols}&h=${
      size * rows
    }&fit=crop&auto=format&dpr=2 2x`,
  };
}

const Art = () => {
  const theme = useTheme();

  const match = useMediaQuery(theme.breakpoints.up('sm'));

  if (match === true) {
    return (
      <Box
        marginX={12}>
      <ImageList
        variant="quilted"
        rowHeight={121}
        cols={6}
      >
        {list.map((item) => (
          <>
          <ImageListItem key={item.img} cols={item.cols || 1} rows={item.rows || 1}>
            <img
              {...srcset(item.img, 121, item.rows, item.cols)}
              alt={item.title}
              loading="lazy"
            />
            <ImageListItemBar
            title={item.title}
            ></ImageListItemBar>
          </ImageListItem>
  
          </>
        ))}
      </ImageList>
      </Box>
    );
  }
  else {
    return (
      <Box
        marginX={12}>
      <ImageList
        variant="quilted"
        rowHeight={121}
        cols={2}
      >
        {list.map((item) => (
          <>
          <ImageListItem key={item.img} cols={item.cols || 1} rows={item.rows || 1}>
            <img
              {...srcset(item.img, 121, item.rows, item.cols)}
              alt={item.title}
              loading="lazy"
            />
            <ImageListItemBar
            title={item.title}
            ></ImageListItemBar>
          </ImageListItem>
  
          </>
        ))}
      </ImageList>
      </Box>
    );
  }


}

